import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {SocialSharing} from "@ionic-native/social-sharing";


@IonicPage()
@Component({
  selector: 'page-item-detail',
  templateUrl: 'notice-detail.html'
})
export class NoticeDetailPage {
  item: any;

  constructor(public navCtrl: NavController, navParams: NavParams,
              private socialSharing: SocialSharing,
              ) {
    this.item = navParams.get('data') || "none";
    console.log(this.item)
  }

    shareWithW(ms: any){
        this.socialSharing.shareViaWhatsApp("*New Announcement* ðŸ”” "+"\n" +"*"+ ms.title +"*"+"\n" + " By: " + ms.name + "\n"+ ms.message).catch(er=>{

        })

    }
    shareWithS(ms){
        this.socialSharing.shareViaWhatsApp("*New Announcement* ðŸ”” "+"\n" + ms.title +"\n" + " By: " + ms.name + "\n"+ ms.message).catch(er=>{

        })

    }
    shareWithF(ms){
        this.socialSharing.shareViaFacebook("*New Announcement* ðŸ”” "+"\n" + ms.title +"\n" + " By: " + ms.name + "\n"+ ms.message).catch(er=>{

        })

    }

}



